@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: 'Roboto', sans-serif;;
}

.lists {
  margin-top: -140px;
}

.loading {
  position: fixed;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 99;
  background-color: black;
  display: flex;
}
header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    transition: all ease 0.5s;
    background: transparent;
}

header.black {
    background-color: #141414;
}

.header--logo img {
    height: 30px;
}

.header--user img {
    height: 40px;
    border-radius: 3px;
}
.featured {
    height: 100vh;
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 5%, transparent 95%);
}

.featured--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 5%, transparent 95%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 50px;
    padding-bottom: 140px;
    padding-top: 70px;
}

.featured--name {
    font-size: 60px;
    font-weight: bold;
}

.featured--info {
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--points {
    color: #46d369;
}

.featured--points,
.featured--year,
.featured--seasons {
    display: inline-block;
    margin-right: 15px;
}

.featured--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured--buttons{
    margin-top: 15px;
}

.featured--watchbutton,
.featured--listbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 15px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
}

.featured--watchbutton:hover,
.featured--listbutton:hover {
    opacity: 0.7;
    transition: all ease 0.2s;
}

.featured--watchbutton {
    background-color: #fff;
    color: #000;
}

.featured--listbutton {
    background-color: #333;
    color: #fff;
}

.featured--genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px) {
    .featured {
        height: 90vh;
    }
    .featured--name {
        font-size: 40px;
        margin-right: 30px;
    }
    .featured--info {
        font-size: 16px;
    }
    .featured--description {
        font-size: 16px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--watchbutton,
    .featured--listbutton {
        font-size: 16px;
    }
    .featured--genres {
        font-size: 16px;
    }

}
.movieRow {
    margin-bottom: 30px;
}

.movieRow--title {
    margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list {
    transition: all ease 0.5s;
}


.movieRow--item {
    display: inline-block;
    width: 200px;
}

.movieRow--item img {
    height: 300px;
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all ease 0.2s;
    cursor: pointer;
}

.movieRow--left,
.movieRow--right {
    position: absolute;
    width: 40px;
    height: 300px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
}

.movieRow--left {
    left: 0;
}

.movieRow--right {
    right: 0;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
    opacity: 1;
    transition: all ease 0.5s;
}

@media (max-width:760px) {
    .movieRow--left,
    .movieRow--right {
        opacity: 1;
    }
}
footer {
    margin: 50px 0;
    text-align: center;
}
p {
    margin: 0;
}

@media (max-width:760px) {
    footer {
        margin: 50px;
    }
}
